@charset "UTF-8";
.item-image-wrapper {
  position: relative;
  overflow: hidden; }

/*
   common
   uncommon
   rare
   mythical
   legendary
   immortal
   ancient
*/
.common::before {
  content: "";
  background: #acc2d7;
  color: #fff;
  position: absolute;
  width: 180px;
  padding: 10px;
  display: block;
  -webkit-transform: rotate(-50deg);
  -moz-transform: rotate(-50deg);
  -ms-transform: rotate(-50deg);
  -o-transform: rotate(-50deg);
  transform: translate3d(0, 0, 1);
  text-align: center;
  bottom: 10px;
  right: -80px;
  font-size: .8em;
  font-weight: 100; }

